<template>
  <div class="login">
    <div class="left">
      <div class="header">
        <router-link :to="{name:'root'}"></router-link>/
        <span></span>
      </div>
      <div class="content">
        <div class="form">
          <Form ref="formInline" :model="formInline" :rules="ruleInline">
            <FormItem prop="user" label=''>
              <Input type="text" v-model="formInline.user" placeholder="Username" />
            </FormItem>
            <FormItem prop="password" label=''>
              <Input type="password" v-model="formInline.password" placeholder="Password" />
            </FormItem>
            <FormItem>
              <Button type="primary" @click="handleSubmit('formInline')"></Button>
            </FormItem>
          </Form>
        </div>
      </div>
    </div>
    <div class="right">
      <div class="header"></div>
      <div class="about">
        <div class="title">
          CNodeNode.js
        </div>
        <div class="text"></div>
        <ul>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
  import { Form, FormItem, Input, Button } from "iview";

  export default {
    components: {
      Form,
      FormItem,
      Input,
      Button
    },
    data() {
      return {
        formInline: {
          user: "",
          password: ""
        },
        ruleInline: {
          user: [
            {
              required: true,
              message: "",
              trigger: "blur"
            }
          ],
          password: [
            {
              required: true,
              message: ".",
              trigger: "blur"
            },
            {
              type: "string",
              min: 6,
              message: "6",
              trigger: "blur"
            }
          ]
        }
      };
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate(valid => {
          if (valid) {
            this.$Message.success("");
          } else {
            this.$Message.error("");
          }
        });
      }
    }
  };
</script>
<style scoped>
  @media (max-width: 980px) {
    .right {
      display: none;
    }
  }
  .login {
    display: flex;
    margin-top: 15px;
  }
  .left {
    flex: 1;
    background: #fff;
    margin-left: 30px;
    margin-right: 30px;
  }
  .right {
    width: 290px;
    font-size: 14px;
    margin-right: 30px;
  }
  .header {
    padding: 10px;
    background-color: #f6f6f6;
    border-radius: 3px 3px 0 0;
    font-size: 14px;
  }
  .content {
    padding: 50px 80px;
    border-radius: 0 0 3px 3px;
  }
  .about {
    font-size: 14px;
    padding: 5px;
    background: #fff;
  }
  .about .title {
    font-size: 14px;
    word-break: break-word;
  }
  .about ul {
    padding-left: 25px;
    padding-top: 10px;
  }
  .form {
    max-width: 560px;
  }
</style>


